<template>
  <lay-header
    :showBack="true"
    :tBarColor="'#FFFFFF'"
    :isGetingItems="isGetingItems"
    v-if="active"
  >
    <v-layout column class="lay-body">
      <span class="txt-top"
        >회원가입을 위해 <br />아래의 정보를 입력해 주세요.</span
      >
      <span class="txt-desc mt-3"
        >입력하신 이메일로 인증 메일이 발송됩니다.</span
      >
      <c-input
        :hint="'이메일 주소를 입력해주세요'"
        :value.sync="inputList.id.value"
        class="mt-8"
      />
      <v-spacer />
      <v-layout class="lay-auth mt-5" align-center>
        <img
          :src="
            isCheck
              ? require('@/assets/images/ic-check-on.svg')
              : require('@/assets/images/ic-check-off.svg')
          "
          @click.prevent.stop="isCheck = !isCheck"
        />
        <span class="ml-2">
          <span
            @click.prevent.stop="
              () => {
                pushRouter({ name: 'terms' });
              }
            "
            >서비스 약관</span
          >
          및
          <span
            @click.prevent.stop="
              () => {
                pushRouter({ name: 'privacy' });
              }
            "
            >개인정보 보호정책</span
          >에 동의 합니다.
        </span>
      </v-layout>
      <c-btn
        :height="48"
        :thema="`default-bg${
          !inputList.id.value || !isCheck ? '-disable' : ''
        }`"
        :fontWeight="400"
        :fontSize="16"
        :readOnly="!inputList.id.value || !isCheck"
        :onClick="onClickActionNext"
        >다음</c-btn
      >
    </v-layout>
    <v-dialog persistent v-model="pop.isShow" :width="pxToRem(282)">
      <p-base
        :title="'이메일이 발송되었습니다.'"
        :titleAlign="'center'"
        :okTxt="'다음'"
        :onOk="onClickActionMailOk"
        :thema="'default'"
      >
        <v-layout column align-center class="text-center">
          <img class="mb-2" src="@/assets/images/ic-mail.svg" />
          이메일을 확인해주세요
        </v-layout>
      </p-base>
    </v-dialog>
    <v-dialog persistent v-model="popSuccess.isShow" :width="pxToRem(334)">
      <p-base :title="'메일 인증을 완료했습니다.'" :titleAlign="'center'">
        <v-layout column align-center class="text-center mb-n2">
          <img src="@/assets/images/ic-mail-check.svg" />
        </v-layout>
      </p-base>
    </v-dialog>
    <v-dialog persistent v-model="popFail.isShow" :width="pxToRem(282)">
      <p-base
        :title="'인증 시간이 초과되었습니다.'"
        :titleAlign="'center'"
        :okTxt="'인증메일 다시보내기'"
        :onOk="() => {onClosePopupAction(popFail);onClickActionNext();}"
      >
        <v-layout column align-center class="text-center mt-3">
          <img src="@/assets/images/ic-mail-fail.svg" />
        </v-layout>
      </p-base>
    </v-dialog>
  </lay-header>
</template>

<script>
export default {
  name: "KeepAlive",
  middleware: "guest",
  data() {
    return {
      active: false,
      isGetingItems: false,
      inputList: {
        id: {
          value: 'hooladhoon@gmail.com',
          validation: () => {
            return this.checkId();
          },
        },
      },
      pop: {
        isCreate: false,
        isShow: false,
      },
      popSuccess: {
        isCreate: false,
        isShow: false,
      },
      popFail: {
        isCreate: false,
        isShow: false,
      },
      isCheck: false,
      userId: null,
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  activated() {
    this.active = true;
    if (!this.$route.meta.internalNavigation) {
      // 새로 들어왔을 경우
      this.inputList.id.value = null;
      this.isCheck = false;
    } else {
      // 데이터만 갱신 (backkey 로 화면진입시)
    }
    this.$route.meta.internalNavigation = true;
  },
  destroyed() {},
  methods: {
    onClickActionNext() {
      if (!this.isCheck) {
        this.showToast("서비스 이용약관 및 개인정보 보호정책에 동의해주세요.");
        return false;
      }
      for (let key in this.inputList) {
        if (this.inputList[key].validation) {
          if (!this.inputList[key].validation()) {
            return false;
          }
        }
      }
      if (this.isGetingItems) {
        return;
      }
      
      this.isGetingItems = true;
      this.api({
            url: "/login/check-email",
            method: "post",
            data: {loginId: this.inputList.id.value},
            noPop: true,
          }).then(data => {
            this.userId = data;
            this.onShowPopupAction(this.pop);
            this.isGetingItems = false;
          }).catch((err) => {
            if(err.code == '10009'){
              this.showPopup({
                title: "기존 회원 알림",
                msg: err.msg,
                okTxt: "로그인하기",
                cancelTxt: "다시 입력하기",
                okAction: () => {
                  this.pushRouter({ name: "login" });
                },
              });
            }else
              this.showToast(err.msg);
            this.isGetingItems = false;
          })
    },
    async onClickActionMailOk() {
      if (this.isGetingItems) {
        return;
      }
      this.isGetingItems = true;

      this.api({
        url: "/login/auth-email",
        method: "post",
        data: {loginId: this.inputList.id.value, userId: this.userId},
      }).then(data => {
        if(data){
          this.onClosePopupAction(this.pop);
          this.onShowPopupAction(this.popSuccess, () =>
          setTimeout(() => {
            this.onClosePopupAction(this.popSuccess);
            this.pushRouter({
              name: "pass",
              params: {
                loginId: this.inputList.id.value,
                userId: this.userId
              }
            });
            }, 3000)
          );
        }
        this.isGetingItems = false;
      }).catch((err) => {
        if(err.code == '10011'){
          this.showToast(err.msg);
        }else if(err.code == '100012'){
          this.onClosePopupAction(this.pop);
          this.onShowPopupAction(this.popFail);
        }else{
          this.onClosePopupAction(this.pop);
        }
        this.isGetingItems = false;
      })
    },
    checkId() {
      if (
        !this.inputList.id.value ||
        !this.validateEmail(this.inputList.id.value)
      ) {
        this.showToast("이메일 주소를 정확히 입력해주세요.");
        return false;
      }
      return true;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/sass/app.scss";
.lay-body {
  padding: pxToRem(20px);
  .txt-top {
    font-style: normal;
    font-weight: 700;
    font-size: pxToRem(24px);
    line-height: pxToRem(30px);
    letter-spacing: -0.022em;
    color: #000000;
  }
  .txt-desc {
    font-style: normal;
    font-weight: 400;
    font-size: pxToRem(15px);
    line-height: pxToRem(20px);
    letter-spacing: -0.022em;
    color: #000000;
  }
  .lay-auth {
    flex: none;
    margin-bottom: pxToRem(18px);
    img {
      width: pxToRem(24px);
      height: pxToRem(24px);
    }
    span {
      font-style: normal;
      font-weight: 400;
      font-size: pxToRem(13px);
      line-height: pxToRem(18px);
      color: #000000;
      opacity: 0.6;
      span {
        opacity: 1;
        text-decoration-line: underline;
      }
    }
  }
}
</style>
